body {
  background-color: rgb(232, 212, 200);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.text-container {
  width: 75%;
  height: auto;
  margin: auto;

  border-color: white;
  border-style: solid;
  border-width: 2px;

}

.login {
  margin: 100px auto auto;
  width: 50%;
  display: block;
  text-align: center;
}

.login-button {
  margin-top: 10px;
}

.helper {
  margin-bottom: 1px;
}
.header {

}

.header h1 {
    color: rgb(0, 0, 0);
    margin-bottom: 0;
}

.headertext {
    margin-bottom: 1.5rem;
}

.headertext p {
    margin-top: 0;
    color: rgb(0, 0, 0);
    font-style: italic;
}

.navBar {
    margin-left: 10px;
    display: inline-flex;
}

button {
    margin-bottom: 10px;
    margin-left: 10px;
    margin-right: 10px;
}

.selected {
    background-color: rgb(222, 203, 187);
    text-decoration: none;
}

.unselected {
    text-decoration: none;
}
.activity {
    margin: 15px;
}

p .activity{
    margin: 0;
}

.program {

}
